<template>
    <div class="panel">
        <b-button size=sm variant="info" to="/user_add?admin=1"
            >添加管理者</b-button
        >
        <b-button size=sm class=ml-4 variant="info" to="/user_add?admin=0"
            >添加使用者</b-button
        >
        <b-table class=mt-3 :items="userlist" :fields="fields" show-empty>
            <template #empty="">
                <p align="center">
                    請使用上面按鈕新增人員
                </p>
            </template>
            <template #cell(actions)="data">
                <b-button-group>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="editUser(data.item)"
                    >
                        <b-icon icon="pen" />
                    </b-button>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="deleteUser(data.item)"
                    >
                        <b-icon icon="trash" />
                    </b-button>
                </b-button-group>
            </template>
        </b-table>
        <b-modal v-model="showInfo" :title="info.title" ok-only>
            {{info.message}}
        </b-modal>
        <b-modal v-model="showEdit" size="lg" title="修改物品" @ok="updateUser">
            <!-- <b-input class="mt-3" v-model="item.role" placeholder="類別" /> -->
            <b-input class="mt-3" v-model="item.realname" placeholder="姓名" />
            <b-input class="mt-3" v-model="item.phone" placeholder="電話" />
            <b-input class="mt-3" v-model="item.email" placeholder="Email" />
            <!-- <b-input class="mt-3" v-model="item.username" placeholder="Username" /> -->
        </b-modal>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data() {
        return {
            fields: [
                { key: "role", label: "類別", sortable: true },
                { key: "realname", label: "姓名", sortable: true },
                { key: "phone", label: "電話", sortable: true },
                { key: "email", label: "Email", sortable: true },
                { key: "username", label: "Username", sortable: true },
                { key: "actions", label: "操作", sortable: false },
            ],
        created() {
        if (!this.curuser) this.$router.push('/login')
    },
            emptyStr: "<p align='center'>Nothing</p>",
            // userlist: [],
            showEdit: false,
            showInfo: false,
            info: {
                title: '',
                message: '',
            },
            item: {
                role: '',
                realname: null,
                phone: null,
                email: '',
                username: '',
            },
        };
    },
    created() {
        if (!this.curuser) this.$router.push('/login')
    },
    computed: {
        ...mapState({
            curuser: (state) => state.curuser,
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
        userlist() {
            if (!this.users) return [];
            return Array.from(Object.entries(this.users), ([key, value]) =>
                Object.assign(value, { id: key })
            );
        },
    },
    methods: {
        getToday() {
            var today = new Date();
            var dd = String(today.getDate()).padStart(2, "0");
            var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
            var yyyy = today.getFullYear();
            return yyyy + mm + dd;
        },
        async updateUser() {
            this.item.updated = this.getToday()
            let uid = this.item.id
            let newItem = Object.assign({}, this.item)
            delete newItem.id
            await this.$store.dispatch('update_item', {
                collection: 'users',
                id: uid,
                value: newItem,
            })
            // this.updateUsers()
            this.info = {
                title:"更新使用者", 
                message:'更新使用者完成！'
            }
            this.showInfo = true
        },
        editUser(userData) {
            this.item = Object.assign({}, userData)
            this.showEdit = true
        },
        async deleteUser(userData) {
            if (userData.role == 'admin') {
                this.info = {
                    title:"刪除使用者", 
                    message:'目前暫不支援管理者刪除功能！'
                }
                this.showInfo = true
                return
            }
            let yn = confirm('確定要刪除嗎？')
            if (yn) await this.$store.dispatch('delete_item', {
                collection: 'users',
                id: userData.id,
            })
        },
    }
};
</script>

<style scoped>
.panel {
    margin: 12px 24px 12px 24px;
}
</style>