<template>
    <div class="panel w-50">
        <b-modal v-model="showInfo" title="訊息通知" ok-only @ok="tolist">
            使用者加入成功！
        </b-modal>
        <b-input class="mt-3" v-model="user.realname" placeholder="輸入姓名" />
        <b-input class="mt-3" v-model="user.phone" placeholder="輸入電話" />
        <b-input class="mt-3" v-model="user.email" placeholder="輸入Email" />
        <div v-if="admin==1">
            <b-input class="mt-3" v-model="user.username" placeholder="輸入Username" />
            <b-input class="mt-3" type="password" v-model="user.password" placeholder="輸入密碼" />
            <b-input class="mt-3" type="password" v-model="user.repassword" placeholder="重覆輸入密碼" />
        </div>
        <p align="center">
            <b-button class="mt-3" variant="info" @click="addUser">送出</b-button>
        </p>
    </div>
</template>

<script>
import { fireDb, fireAuth } from '@/plugins/firebase'
export default {
    data() {
        return {
            showInfo: false,
            user: {
                realname: '',
                username: '',
                phone: '',
                email: '',
                password: '',
                repassword: '',
            },
        }
    },
    computed: {
        admin() { return this.$route.query.admin === '1' },
    },
    methods: {
        tolist() {
            this.$router.push('/user_list')
        },  
        checkValid() {
            if (this.user.realname === ''||this.user.phone === '') {
                alert("Name/Phone should not be empty!")
                return false
            }
            if (this.admin) {
                if (this.user.username === '') {
                    alert("Username should not be empty!")
                    return false
                }
                if (this.user.password.length < 6) {
                    alert("Password should be at least 6 chars!")
                    return false
                }
                if (this.user.password === '') {
                    alert("Password should not be empty!")
                    return false
                }
                if (this.user.password !== this.user.repassword) {
                    alert("Password should be same!")
                    return false
                }
            }
            return true
        },
        async addUser() {
            if (!this.checkValid()) return
            try {
                let record = { 
                    realname: this.user.realname, 
                    phone: this.user.phone,
                    email: this.user.email,
                    role: 'user',
                }
                if (this.admin) {
                    record.username = this.user.username,
                    record.role = 'admin'
                    let userCredential = await fireAuth.createUserWithEmailAndPassword(this.user.email, this.user.password)
                    await fireDb.collection('users').doc(userCredential.user.uid).set(record)
                    this.$store.commit('insert_item', {
                        collection: 'users',
                        id: userCredential.user.uid,
                        value: record,
                    })
                } else {
                    await this.$store.dispatch('insert_item', {
                        collection: 'users',
                        value: record,
                    })
                }
                this.showInfo = true
            } catch(e) {
                console.log("Error", e)
            }
        },
    },
}
</script>
    
<style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>