<template>
    <div class="panel w-50">
        <b-input class="mt-3" v-model="item.name" placeholder="物品名稱" />
        <b-input class="mt-3" v-model="item.account" placeholder="財會財產" />
        <b-input class="mt-3" v-model="item.price" placeholder="價格" />
        <b-input class="mt-3" v-model="item.year" placeholder="製造年份" />
        <b-input class="mt-3" v-model="item.note" placeholder="備註" />
        <p align="center">
            <b-button class="mt-3" variant="info" to="/item_list">取消</b-button>
            <b-button class="mt-3 ml-4" variant="info" @click="addItem">新增</b-button>
        </p>
        <b-modal v-model="showItem" title="添加成功！" ok-only @ok="reset">
            <div align="center">
                <p class="mt-2">名稱 = {{item.name}}</p>
                <p class="mt-3">{{newID}}</p>
                <vue-qrcode class="mt-1" :value="newID" :options="{ width: 200 }"></vue-qrcode>
            </div>
        </b-modal>
    </div>
</template>

<script>
import VueQrcode from '@chenfengyuan/vue-qrcode';
export default {
    data() {
        return {
            item: {
                name: '',
                account: '',
                price: null,
                year: null,
                note: '',
                updated: '',
            },
            showItem: false,
            newID: '',
        }
    },
    
    components: {
        VueQrcode,
    },
    methods: {
        reset() {
            this.$router.push("/item_list")
        },
        getToday() {
            var today = new Date();
            var dd = String(today.getDate()).padStart(2, "0");
            var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
            var yyyy = today.getFullYear();
            return yyyy + mm + dd;
        },
        checkValid() {
            if (this.item.name === '') {
                alert("名稱不能為空白！")
                return false
            }
            return true
        },
        async addItem() {
            if (!this.checkValid()) return
            this.item.updated = this.getToday()
            try {
                this.newID = await this.$store.dispatch('insert_item',{
                    collection: 'items',
                    value: this.item,
                })
                if (this.newID) this.showItem = true
            } catch(e) {
                console.log("Error", e)
            }
        },
    },
}
</script>
    
<style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>