<template>
    <div class="panel w-50 mt-4">
        <b-select v-model="borrow.user_id" :options="userlist"></b-select>
        <div class="d-flex mt-3">
            <div style="width:100px">編碼</div>
            <b-input
                v-model="borrow.item_id"
                placeholder="Item id"
                list="itemids" 
            />
            <datalist id="itemids">
                <option v-for="x in toborrow" :key="x" :value="x" />
            </datalist>
            <b-button size="sm" @click="scan=true">Scan</b-button>
        </div>
        <div class="d-flex mt-2">
            <span style="width:100px">名稱</span>
            <div>{{name}}</div>
        </div>
        <p align="center">
            <b-button class="mt-3" variant="info" @click="borrowAdd">借用</b-button>
        </p>
        <div v-if="scan">
            <qrcode-stream @init="onInit" @decode="onDecode"></qrcode-stream>
            <p align="center">
                <b-button variant=info class=mt-2 @click="scan=false">Close</b-button>
            </p>
        </div>
        <b-modal v-model="showInfo" title="借用成功" @ok="reset">
            已登記借用！
        </b-modal>
    </div>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";
import { mapState } from "vuex";
export default {
    data() {
        return {
            scan: false,
            borrow: {
                user_id: null,
                item_id: null,
            },
            showInfo: false,
        };
    },
    
    created() {
        if (!this.curuser) this.$router.push('/login')
    },
    components: { QrcodeStream },
    computed: {
        ...mapState({
            curuser: (state) => state.curuser,
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
        name() {
            if (this.items && this.borrow.item_id) {
                return this.items[this.borrow.item_id].name
            }
            return null
        },
        used() {
            let result = []
            if (this.borrows) {
                for (let key in this.borrows) {
                    let item = this.borrows[key]
                    console.log("BITEM", item)
                    console.log("BITEMID", item.item_id)
                    result.push(item.item_id)
                }
            }
            return result
        },
        toborrow() {
            if (!this.items) return []
            console.log("USED", this.used)
            console.log("ITEMS", this.items)
            let result = Object.keys(this.items).filter(item => !this.used.includes(item))
            console.log("result", result)
            return result
        },
        userlist() {
            let result = [{ value: null, text: "Select User" }];
            if (!this.users) return result;
            return result.concat(
                Array.from(Object.entries(this.users), ([key, value]) => ({
                    value: key,
                    text: value.realname,
                }))
            );
        },
    },
    methods: {
        reset() {
            this.borrow = {
                user_id: null,
                item_id: null,
            }
        },
        async onInit (promise) {
            try {
                await promise
                // successfully initialized
            } catch (error) {
                console.log("ERROR", error)
                this.scan = false
            }
        },
        onDecode(result) {
            this.borrow.item_id = result
            this.scan = false
        },
        checkValid() {
            if (!this.borrow.user_id || this.borrow.item_id === "") {
                alert("Name or item id should not be empty!");
                return false;
            }
            return true;
        },
        async borrowAdd() {
            if (!this.checkValid()) return;
            try {
                await this.$store.dispatch('insert_item', {
                    collection: 'borrows',
                    value: this.borrow,
                })
                this.showInfo = true
            } catch (e) {
                console.log("Error", e)
            }
        },
    },
};
</script>
  <style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>