<template>
    <div class="panel w-50 mt-4">
        <div class="d-flex mt-3">
            <div style="width:100px">編碼</div>
            <b-input
                v-model="item_id"
                placeholder="Item id"
                list="itemids" 
            />
            <datalist id="itemids">
                <option v-for="x in used" :key="x" :value="x" />
            </datalist>
            <b-button size="sm" @click="scan=true">Scan</b-button>
        </div>
        <div class="d-flex mt-2">
            <span style="width:100px">借用者</span>
            <div>{{username}}</div>
        </div>
        <div class="d-flex mt-2">
            <span style="width:100px">名稱</span>
            <div>{{itemname}}</div>
        </div>
        <p align="center">
            <b-button class="mt-3" variant="info" @click="returnItem">歸還</b-button>
        </p>
        <div v-if="scan">
            <qrcode-stream @init="onInit" @decode="onDecode"></qrcode-stream>
            <p align="center">
                <b-button variant=info class=mt-2 @click="scan=false">Close</b-button>
            </p>
        </div>
        <b-modal v-model="showInfo" title="歸還成功" @ok="item_id=''">
            已登記歸還！
        </b-modal>
    </div>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";
import { mapState } from "vuex";
export default {
    data() {
        return {
            scan: false,
            item_id: null,
            showInfo: false,
        };
    },
    created() {
        if (!this.curuser) this.$router.push('/login')
    },
    components: { QrcodeStream },
    computed: {
        ...mapState({
            curuser: (state) => state.curuser,
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
        borrow_id() {
            if (this.item_id==='' || !this.borrows) return null
            for(let key in this.borrows) {
                let item = this.borrows[key]
                if (item.item_id === this.item_id) {
                    return key
                }
            }
            return null
        },
        username() {
            if (!this.users || !this.item_id) return null
            if (!this.borrow_id) return null
            let bitem = this.borrows[this.borrow_id]
            return this.users[bitem.user_id].realname
        },
        itemname() {
            if (this.items && this.item_id) {
                return this.items[this.item_id].name
            }
            return null
        },
        used() {
            let result = []
            if (this.borrows) {
                for (let key in this.borrows) {
                    let item = this.borrows[key]
                    console.log("BITEM", item)
                    console.log("BITEMID", item.item_id)
                    result.push(item.item_id)
                }
            }
            console.log("USED", result)
            return result
        },
        userlist() {
            let result = [{ value: null, text: "Select User" }];
            if (!this.users) return result;
            return result.concat(
                Array.from(Object.entries(this.users), ([key, value]) => ({
                    value: key,
                    text: value.realname,
                }))
            );
        },
    },
    methods: {
        async onInit (promise) {
            try {
                await promise
                // successfully initialized
            } catch (error) {
                console.log("ERROR", error)
                this.scan = false
            }
        },
        onDecode(result) {
            this.item_id = result
            this.scan = false
        },
        checkValid() {
            if (this.item_id === "") {
                alert("Encoding should not be empty!");
                return false;
            }
            return true;
        },
        async returnItem() {
            if (!this.checkValid()) return;
            try {
                await this.$store.dispatch('delete_item', {
                    collection: 'borrows',
                    id: this.borrow_id,
                })
                this.showInfo = true
            } catch (e) {
                console.log("Error", e)
            }
        },
    },
};
</script>
  <style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>