<template>
    <div class="panel">
        <VueHtml2pdf
            :show-layout="false"
            :float-layout="true"
            :enable-download="true"
            :preview-modal="true"
            :paginate-elements-by-height="1400"
            filename="myPDF"
            :pdf-quality="2"
            :manual-pagination="false"
            pdf-format="a4"
            pdf-orientation="portrait"
            ref="html2Pdf"
        >
            <!-- pdf-content-width="800px" -->
            <section slot="pdf-content">
                <!-- PDF Content Here -->
                <div class="d-flex justify-content-around flex-wrap">
                    <div class='qritem' v-for="item in selectitems" :key="item.id">
                        <vue-qrcode class="mt-1" :value="item.id" 
                            :options="{ width: 320 }">
                        </vue-qrcode>                
                        <p>{{item.id}}</p>
                        <p>{{item.name}}</p>
                    </div>
                </div>
            </section>
        </VueHtml2pdf>
        <b-button size=sm variant="info" to="/item_add?admin=1"
            >新增物品</b-button
        >
        <b-button size=sm class=ml-4 variant="info" @click="qrcode"
            >QR Code</b-button
        >
        <b-table class=mt-3 :items="itemlist" :fields="fields" 
            show-empty striped selectable select-mode="range"
            @row-selected="onRowSelected" >
            <template #empty="">
                <p align="center">
                    Nothing<br /><br />
                    <b-button variant="info" to="/item_add">Add Item</b-button>
                </p>
            </template>
            <template v-slot:cell(actions)="row">
                <b-button-group>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="editItem(row.item)"
                    >
                        <b-icon icon="pen" />
                    </b-button>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="deleteItem(row.item)"
                    >
                        <b-icon icon="trash" />
                    </b-button>
                </b-button-group>
            </template>
        </b-table>
        <b-modal v-model="showEdit" size="lg" title="修改物品" @ok="updateItem">
            <b-input class="mt-3" v-model="item.name" placeholder="物品名稱" />
            <b-input class="mt-3" v-model="item.account" placeholder="財會財產" />
            <b-input class="mt-3" v-model="item.price" placeholder="價格" />
            <b-input class="mt-3" v-model="item.year" placeholder="製造年份" />
            <b-input class="mt-3" v-model="item.note" placeholder="備註" />
        </b-modal>

    </div>
</template>

<script>
import { mapState } from "vuex";
import VueHtml2pdf from 'vue-html2pdf'
import VueQrcode from '@chenfengyuan/vue-qrcode';
 
export default {
    components: {
        VueHtml2pdf, VueQrcode,
    },
    data() {
        return {
            selectitems: [],
            showPDF: false,
            fields: [
                { key: "name", label: "物品名稱", sortable: true },
                { key: "account", label: "財會財產", sortable: true },
                { key: "price", label: "價格", sortable: true },
                { key: "year", label: "年份", sortable: true },
                { key: "note", label: "備註", sortable: false },
                { key: "updated", label: "更新時間", sortable: true },
                { key: "actions", label: "動作", sortable: false },
            ],
            emptyStr: "<p align='center'>Nothing</p>",
            itemlist: [],
            showEdit: false,
            item: {
                name: '',
                account: '',
                price: null,
                year: null,
                note: '',
                updated: '',
            },
        };
    },
    created() {
        if (!this.curuser) this.$router.push('/login')
        this.updateItems()
    },
    computed: {
        ...mapState({
            curuser: (state) => state.curuser,
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
    },

    methods: {
        onRowSelected(s) {
            this.selectitems = s
        },
        qrcode() {
            this.$refs.html2Pdf.generatePdf()
            // console.log("SI", this.selectitems)
        },
        getToday() {
            var today = new Date();
            var dd = String(today.getDate()).padStart(2, "0");
            var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
            var yyyy = today.getFullYear();
            return yyyy + mm + dd;
        },
        async updateItem() {
            this.item.updated = this.getToday()
            let newItem = Object.assign({}, this.item)
            delete newItem.id
            await this.$store.dispatch('update_item', {
                collection: 'items',
                id: this.item.id,
                value: newItem,
            })
            this.updateItems()
        },
        obj2list(myobj) {
            let result = []
            for (let key in myobj) {
                let item = myobj[key]
                item.id = key
                result.push(item)
            }
            return result
        },
        updateItems() {
            if (!this.items) {
                this.itemlist = []
            } else {
                this.itemlist = this.obj2list(this.items)
            }
        },
        editItem(itemData) {
            this.item = Object.assign({}, itemData)
            this.showEdit = true
        },
        async deleteItem(itemData) {
            console.log("iD", itemData)
            let yn = confirm('確定要刪除嗎？')
            if (yn) await this.$store.dispatch('delete_item', {
                collection: 'items',
                id: itemData.id,
            })
            this.updateItems()
        },
    },
};
</script>
  
<style scoped>
.qritem {
    font-size: 14px;
    text-align: center;
    width: 300px;
}
.panel {
    margin: 12px 24px 12px 24px;
}
</style>